import {
    Component, Input, Output, EventEmitter, ElementRef, ViewChild,
    forwardRef, AfterViewInit, ViewEncapsulation, HostListener, Renderer2
} from '@angular/core';
import * as moment from 'moment';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { NhDateLocale } from './nh-date.locale.config';
import 'moment/locale/vi';

import { NhDateUtils } from './nh-date.utils';
import { Moment } from 'moment';

class NhDay {
    day: number;
    month: number;
    year: number;
    hours: number;
    minutes: number;

    constructor(year: number, month: number, day: number, hours?: number, minutes?: number) {
        this.day = day;
        this.month = month;
        this.year = year;
        this.hours = hours ? hours : 0;
        this.minutes = minutes ? minutes : 0;
    }
}

@Component({
    selector: 'nh-date',
    templateUrl: './nh-date.component.html',
    styleUrls: ['./nh-date.scss'],
    providers: [
        {provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => NhDateComponent), multi: true}
    ],
    encapsulation: ViewEncapsulation.None
})
export class NhDateComponent implements AfterViewInit, ControlValueAccessor {
    @Input() themeColor: 'dark' | 'blue' | 'white' | 'green' = 'green';
    @Input() disabled = false;
    @Input() material = false;
    @Input() type: 'input' | 'inputButton' | 'inline' | 'link' = 'input';
    @Input() format = 'DD/MM/YYYY';
    @Input() placeholder = 'Vui lng chn ngy thng';
    @Input() showTime = false;
    @Input() allowRemove = true;
    @Input() icon = 'fa fa-calendar';
    @Input() removeIcon = 'fa fa-times';
    @Input() position: 'above' | 'below' | 'left' | 'right' | 'auto' = 'auto';
    @Input() mask: boolean | string;
    @Output() selectedDateEvent = new EventEmitter();
    @Output() removedDateEvent = new EventEmitter();

    private _locale = 'vi';
    private _months = NhDateLocale[this.locale].months;
    private _dayOfWeek = NhDateLocale[this.locale].dayOfWeek;
    private _dayOfWeekShort = NhDateLocale[this.locale].dayOfWeekShort;
    private _value = moment();
    private _separator = '/';
    private _hours = '00';
    private _minutes = '00';
    private _mask = '';
    private _caretPosition = 0;
    private _KEY0 = 48;
    private _KEY9 = 57;
    private __KEY0 = 96;
    private __KEY9 = 105;
    private _CTRLKEY = 17;
    private _DEL = 46;
    private _ENTER = 13;
    private _ESC = 27;
    private _BACKSPACE = 8;
    private _ARROWLEFT = 37;
    private _ARROWUP = 38;
    private _ARROWRIGHT = 39;
    private _ARROWDOWN = 40;
    private _TAB = 9;
    private _F5 = 116;
    private _AKEY = 65;
    private _CKEY = 67;
    private _VKEY = 86;
    private _ZKEY = 90;
    private _YKEY = 89;
    private _ctrlDown = false;
    // private _inputValue: string;
    private _isShowDate = false;
    private _inputDateTimeAllowedFormat = {
        vi: [
            'DD/MM/YYYY',
            'DD/MM/YYYY HH:mm',
            'DD/MM/YYYY HH:mm:ss',
            'DD/MM/YYYY HH:mm',
            'DD-MM-YYYY',
            'DD-MM-YYYY HH:mm',
            'DD-MM-YYYY HH:mm:ss',
            'DD-MM-YYYY HH:mm',
        ],
        en: [
            'MM/DD/YYYY',
            'MM/DD/YYYY HH:mm',
            'MM/DD/YYYY HH:mm:ss',
            'MM/DD/YYYY HH:mm',
            'MM-DD-YYYY',
            'MM-DD-YYYY HH:mm',
            'MM-DD-YYYY HH:mm:ss',
            'MM-DD-YYYY HH:mm',
        ]
    };
    private _originalValue: Moment;

    private _numberRegex = /^[-+]?(\d+|\d+\.\d*|\d*\.\d+)$/;

    @ViewChild('dateContainer') dateContainer: ElementRef;
    @ViewChild('dateWrapper') dateWrapper: ElementRef;
    @ViewChild('nhDateInputElement') nhDateInputElement: ElementRef;

    @Input()
    set locale(value: string) {
        this._locale = value;
    }

    get locale() {
        return this._locale;
    }

    @Input()
    set months(months: string[]) {
        this._months = months;
    }

    get months() {
        return this._months;
    }

    @Input()
    set dayOfWeek(dayOfWeek: string[]) {
        this._dayOfWeek = dayOfWeek;
    }

    get dayOfWeek() {
        return this._dayOfWeek;
    }

    @Input()
    set dayOfWeekShort(dayOfWeekShort: string[]) {
        this._dayOfWeekShort = dayOfWeekShort;
    }

    get dayOfWeekShort() {
        return this._dayOfWeekShort;
    }

    set hours(value: string) {
        const hour = parseInt(value);
        this._hours = !this._numberRegex.test(value) || hour > 23 || hour < 0 ? '00' : hour < 10 ? `0${hour}` : hour.toString();
        this.value.hour(hour);
        this.selectedDate = this.value.format(this.format);
    }

    get hours() {
        return this._hours;
    }

    set minutes(value: string) {
        const minute = parseInt(value);
        this._minutes = !this._numberRegex.test(value) || minute > 59 || minute < 0 ? '00' : minute < 10 ? `0${minute}` : minute.toString();
        this.value.minute(minute);
        this.selectedDate = this.value.format(this.format);
    }

    get minutes() {
        return this._minutes;
    }

    @Input()
    set value(date: any) {
        if (date) {
            const isMoment = moment.isMoment(date);
            if (isMoment) {
                this._value = date;
            } else {
                this._value = moment(date, this._inputDateTimeAllowedFormat[this.locale]);
            }
        } else {
            this._value = null;
        }
        this.setSelectedValue();
    }

    get value() {
        return this._value;
    }

    get caretPosition() {
        return this._caretPosition;
    }

    set caretPosition(value: number) {
        this._caretPosition = value;
        NhDateUtils.setCaretPos(this.nhDateInputElement, this.caretPosition);
    }

    get isShowDate() {
        return this._isShowDate;
    }

    set isShowDate(value: boolean) {
        this._isShowDate = value;
        if (value) {
            this.calculatePosition();
        }
    }

    name: string;
    selectedMonth: number;
    selectedYear: number;
    selectedDay: number;
    selectedDate = '';
    isNext = false;
    isPrevious = false;
    isZoomIn = false;
    isZoomOut = false;
    isShowYearPicker = false;
    isShowMonthPicker = false;
    listRows = [];
    // listMonth = [];
    listYear = [];
    listDay = [];

    propagateChange: any = () => {
    }

    constructor(private el: ElementRef,
                private renderer: Renderer2) {
        this.name = (new Date().getTime() * Math.ceil(Math.random() * 1000)).toString();
        const today = moment();
        this.value.date(today.date());
        this.value.month(today.month());
        this.value.year(today.year());
        this.value.hours(0);
        this.value.minute(0);
        this.selectedYear = today.year();
        this.selectedMonth = today.month();
        this.selectedDay = today.date();

        // Init list year.
        for (let i = 1950; i <= this.selectedYear + 20; i++) {
            this.listYear.push(i);
        }
    }

    ngAfterViewInit() {
        this.initMask();
    }

    @HostListener('document:click', ['$event'])
    clickOutSide(event) {
        if (this.isShowDate === false) {
            return;
        }
        if (!this.el.nativeElement.contains(event.target)) {
            this.isShowDate = false;
            if (this.mask) {
                // this.selectedDate = this._inputValue;
                this.emitDateValue(this.selectedDate);
            }
        }
    }

    showDate(isShow: boolean = false) {
        this.isShowDate = isShow;
        this.renderListDay();
    }

    removeDate() {
        // this._inputValue = null;
        this.selectedDate = null;
        this.propagateChange(null);
        this.removedDateEvent.emit();
        if (this.mask) {
            this.selectedDate = this._mask;
        }
    }

    next() {
        if (this.selectedMonth < 11) {
            this.selectedMonth += 1;
        } else {
            this.selectedYear += 1;
            this.selectedMonth = 0;
        }
        this.renderListDay();
        this.setZoomInAnimate();
    }

    back() {
        if (this.selectedMonth === 0) {
            this.selectedMonth = 11;
            this.selectedYear -= 1;
        } else {
            this.selectedMonth -= 1;
        }
        this.renderListDay();
        this.setZoomInAnimate();
    }

    selectDay(date?: NhDay) {
        this.value = moment().year(date.year).month(date.month).date(date.day).hours(0).minutes(0).second(0);
        this.setSelectedValue();
        if (!this.showTime) {
            this.isShowDate = false;
            this.emitDateValue();
        }
    }

    acceptChange() {
        this.isShowDate = false;
        this.emitDateValue();
    }

    cancel() {
        this.isShowDate = false;
    }

    selectMonth(month) {
        this.isShowMonthPicker = false;
        if (month !== this.selectedMonth) {
            this.selectedMonth = month;
            this.renderListDay();
        }
    }

    selectYear(year) {
        this.isShowYearPicker = false;
        if (year !== this.selectedYear) {
            this.selectedYear = year;
            this.renderListDay();
        }
    }

    showYear() {
        this.isShowYearPicker = !this.isShowYearPicker;
        this.isShowMonthPicker = false;
        this.setZoomInAnimate();
    }

    showMonth() {
        this.isShowYearPicker = false;
        this.isShowMonthPicker = !this.isShowMonthPicker;
        this.setZoomInAnimate();
    }

    updateHours(isIncrease: boolean = false) {
        let hourNumber = parseInt(this.hours);
        if (isIncrease) {
            if (hourNumber === 23) {
                hourNumber = 0;
                this.hours = '01';
            } else {
                hourNumber += 1;
            }
        } else {
            if (hourNumber === 0) {
                hourNumber = 23;
                this.hours = '23';
            } else {
                hourNumber -= 1;
            }
        }
        this.hours = hourNumber.toString();
    }

    updateMinutes(isIncrease: boolean = false) {
        let minuteNumber = parseInt(this.minutes);
        if (isIncrease) {
            if (minuteNumber === 59) {
                minuteNumber = 0;
                this.minutes = '00';
                this.updateHours(true);
            } else {
                minuteNumber += 1;
            }
        } else {
            if (minuteNumber === 0) {
                minuteNumber = 59;
                this.minutes = '59';
                this.updateHours();
            } else {
                minuteNumber -= 1;
            }
        }
        // this.minutes = minuteNumber > 9 ? minuteNumber.toString() : `0${minuteNumber.toString()}`;
        this.minutes = minuteNumber.toString();
    }

    closeDate() {
        this.isShowDate = false;
    }

    registerOnChange(fn) {
        this.propagateChange = fn;
    }

    writeValue(value) {
        this.value = value;
        if (!value) {
            this._originalValue = null;
        } else {
            if (moment.isMoment(value)) {
                this._originalValue = value;
            } else {
                this._originalValue = moment(value, this._inputDateTimeAllowedFormat[this.locale]);
            }
        }
    }

    registerOnTouched() {
    }

    private getDayOfWeek(year: number, month: number, day: number): number {
        const date = new Date(year, month, day);
        return date.getDay();
    }

    private renderListDay() {
        this.listDay = [];
        this.listRows = [];
        const dayOfWeek = this.getDayOfWeek(this.selectedYear, this.selectedMonth, 1);
        const totalDay = moment(new Date(this.selectedYear, this.selectedMonth, this.selectedDay)).daysInMonth();
        const firstDay = new Date(this.selectedYear, this.selectedMonth, 1);
        const lastDay = new Date(this.selectedYear, this.selectedMonth, totalDay);
        const lastDayOfWeek = this.getDayOfWeek(this.selectedYear, this.selectedMonth, totalDay);

        for (let day = 1; day <= totalDay; day++) {
            this.listDay.push(new NhDay(this.selectedYear, this.selectedMonth, day));
        }

        if (dayOfWeek > 0) {
            const previousDay = moment(firstDay).add(-dayOfWeek, 'day');
            const totalDayInPreviousMonth = previousDay.daysInMonth();
            for (let day = totalDayInPreviousMonth; day >= previousDay.date(); day--) {
                this.listDay.unshift(new NhDay(previousDay.year(), previousDay.month(), day));
            }
        }

        if (lastDayOfWeek < 6) {
            const nextDay = moment(lastDay).add(6 - lastDayOfWeek, 'day');
            for (let day = 1; day <= nextDay.date(); day++) {
                this.listDay.push(new NhDay(nextDay.year(), nextDay.month(), day));
            }
        }

        const rows = Math.ceil(this.listDay.length / 7);
        for (let row = 1; row <= rows; row++) {
            const dates = [];
            this.listDay.forEach((item, index) => {
                if (index < (row * 7) && index >= ((row - 1) * 7)) {
                    dates.push(item);
                }
            });

            this.listRows[row] = dates;
        }
    }

    private setZoomInAnimate() {
        this.isZoomIn = true;
        setTimeout(() => {
            this.isZoomIn = false;
        }, 800);
    }

    private formatDateByRegex(text: string, regex: RegExp): string {
        return text.replace(regex, (match: any, p1: any, p2: any, p3: any) => {
            let str = p1;
            if (p2) {
                str += '/' + p2;
            }
            if (p3) {
                str += '/' + p3;
            }
            return str;
        });
    }

    private formatDateTimeByRegex(text: string, regex: RegExp) {
        return text.replace(regex, (match: any, p1: any, p2: any, p3: any, p4: any, p5: any) => {
            let str = p1;
            if (p2) {
                str += '/' + p2;
            }
            if (p3) {
                str += '/' + p3;
            }
            if (p4) {
                str += ' ' + p4;
            }
            if (p5) {
                str += ':' + p5;
            }
            return str;
        });
    }

    private formatTimeByRegex(text: string, regex: RegExp) {
        return text.replace(regex, (match: any, p1: any, p2: any) => {
            let str = p1;
            if (p2) {
                str += ':' + p2;
            }
            return str;
        });
    }

    private refreshUiByLocale() {
        this.months = NhDateLocale[this.locale].months;
        this.dayOfWeek = NhDateLocale[this.locale].dayOfWeek;
        this.dayOfWeekShort = NhDateLocale[this.locale].dayOfWeekShort;
    }

    private emitDateValue(date?: string) {
        if (this.mask && date === this._mask) {
            return;
        }

        if (date) {
            if (this._originalValue && date === this._originalValue.format(this.format)) {
                return;
            }

            this.propagateChange(date);
            this.selectedDateEvent.emit({
                previousValue: this._originalValue ? this._originalValue.format(this.format) : null,
                currentValue: date
            });
        } else {
            if (this.value === this._originalValue) {
                return;
            }
            this.propagateChange(this.value ? this.value.format(this.format) : null);
            this.selectedDateEvent.emit({
                previousValue: this._originalValue ? this._originalValue.format(this.format) : null,
                currentValue: this.value ? this.value.format(this.format) : null
            });
        }
    }

    private setSelectedValue() {
        if (this.value && this.value.isValid()) {
            this.selectedDay = this.value.date();
            this.selectedMonth = this.value.month();
            this.selectedYear = this.value.year();
            this.hours = this.value.hours().toString();
            this.minutes = this.value.minutes().toString();
            this.selectedDate = this.value.format(this.format);
        } else {
            if (this.mask) {
                this.selectedDate = this._mask;
            } else {
                this.selectedDate = null;
            }
        }
    }

    private generateRegex(mask: string) {
        const reg = mask
            .replace(/([\[\]\/\{\}\(\)\-\.\+]{1})/g, '\\$1')
            .replace(/_/g, '{digit+}')
            .replace(/([0-9]{1})/g, '{digit$1}')
            .replace(/\{digit([0-9]{1})\}/g, '[0-$1_]{1}')
            .replace(/\{digit[\+]\}/g, '[0-9_]{1}');
        return reg;
    }

    private calculatePosition() {
        if (this.position === 'auto') {
            setTimeout(() => {
                const clientBound = this.el.nativeElement.getBoundingClientRect();
                const windowInnerWidth = window.innerWidth;
                const windowInnerHeight = window.innerHeight;
                const dateContainerBound = this.dateContainer.nativeElement.getBoundingClientRect();
                const leftDistance = windowInnerWidth - clientBound.left;
                const botDistance = windowInnerHeight - clientBound.bottom;

                if (leftDistance < dateContainerBound.width) {
                    this.renderer.setStyle(this.dateContainer.nativeElement, 'left', `-${dateContainerBound.width - clientBound.width}px`);
                }

                if (botDistance < dateContainerBound.height) {
                    this.renderer.setStyle(this.dateContainer.nativeElement, 'top', `-${dateContainerBound.height}px`);
                }
            });
        }
    }

    private initMask() {
        if (!this.mask) {
            return;
        }

        if (typeof this.mask === 'boolean') {
            this._mask = this.format
                .replace(/Y{4}/g, '9999')
                .replace(/Y{2}/g, '99')
                .replace(/M{2}/g, '19')
                .replace(/D{2}/g, '39')
                .replace(/H{2}/g, '29')
                .replace(/m{2}/g, '59')
                .replace(/s{2}/g, '59');

            this._mask = this._mask.replace(/[0-9]/g, '_');
            // this._inputValue = this._mask;
        } else if (typeof this.mask === 'string') {
            this._mask = this.mask;
            // this._inputValue = this._mask;
            if (!NhDateUtils.isValidValue(this.mask, this.el.nativeElement.value)) {
                this.el.nativeElement.value = this._mask.replace(/[0-9]/g, '_');
                NhDateUtils.setCaretPos(this.el, 0);
            }
        }

        if (typeof this._mask !== 'undefined' && !this.selectedDate) {
            setTimeout(() => {
                this.selectedDate = this._mask;
            }, 1);
        }

        // Add event listener
        this.renderer.listen(this.nhDateInputElement.nativeElement, 'keydown', (e) => {
            const key = e.which;
            let val = this.selectedDate;
            if (((key >= this._KEY0 && key <= this._KEY9) || (key >= this.__KEY0 && key <= this.__KEY9))
                || (key === this._BACKSPACE || key === this._DEL)) {
                let pos = NhDateUtils.getCaretPos(this.nhDateInputElement);
                let digit = (key !== this._BACKSPACE && key !== this._DEL) ?
                    String.fromCharCode((this.__KEY0 <= key && key <= this.__KEY9) ? key - this._KEY0 : key) : '_';
                if ((key === this._BACKSPACE || key === this._DEL) && pos) {
                    pos -= 1;
                    digit = '_';
                }
                while (/[^0-9_]/.test(this._mask.substr(pos, 1)) && pos < this._mask.length && pos > 0) {
                    pos += (key === this._BACKSPACE || key === this._DEL) ? -1 : 1;
                }
                val = val.substr(0, pos) + digit + val.substr(pos + 1);
                if ($.trim(val) === '') {
                    val = this._mask.replace(/[0-9]/g, '_');
                    return false;
                } else {
                    if (pos === this._mask.length) {
                        e.preventDefault();
                        e.stopPropagation();
                        return false;
                    }
                }

                pos += (key === this._BACKSPACE || key === this._DEL) ? 0 : 1;
                while (/[^0-9_]/.test(this._mask.substr(pos, 1)) && pos < this._mask.length && pos > 0) {
                    pos += (key === this._BACKSPACE || key === this._DEL) ? -1 : 1;
                }
                if (NhDateUtils.isValidValue(this._mask, val)) {
                    this.nhDateInputElement.nativeElement.value = val;
                    this.selectedDate = val;
                    this.caretPosition = pos;
                } else if ($.trim(val) === '') {
                    this.selectedDate = this._mask.replace(/[0-9]/g, '_');
                } else {
                    // input.trigger('error_input.xdsoft');
                }
            } else {
                if (([this._AKEY, this._CKEY, this._VKEY, this._ZKEY, this._YKEY].indexOf(key) !== -1 && this._ctrlDown) ||
                    [this._ESC, this._ARROWUP, this._ARROWDOWN, this._ARROWLEFT, this._ARROWRIGHT, this._F5,
                        this._CTRLKEY, this._TAB, this._ENTER].indexOf(key) !== -1) {
                    return true;
                }
            }
            e.preventDefault();
            e.stopPropagation();
            return false;
        });
    }
}
